.a11y-sr-only,
.a11y-kb-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.a11y-kb-only:active,
.a11y-kb-only:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}
@font-face {
  font-family: 'google-material-icon-font-custom';
  src: url(data:application/x-font-ttf;charset=utf-8;base64,) format('truetype'), url(data:application/font-woff;charset=utf-8;base64,) format('woff');
}
html {
  font-family: Arial, sans-serif;
}
html,
body {
  margin: 0;
  padding: 0;
}
/**
 * App Viewer
 *	* namespace: `viewer-` is used as prefix for all style rules affecting this component and it's descendant.
 *	* concept: feel of fullscreen app with viewport (frame) for previews, a menu bar (header) and overlay panel (info).
 */
html,
.viewer {
  overflow: hidden;
}
.viewer-header {
  *zoom: 1;
  padding: 0;
  background-color: #d3d3d3;
  height: 50px;
  overflow: hidden;
}
.viewer-header:before,
.viewer-header:after {
  display: table;
  content: "";
}
.viewer-header:after {
  clear: both;
}
.viewer-header.is-small {
  width: 58px;
}
.viewer-module-selector,
.viewer-logo {
  float: left;
}
.viewer-module-selector button:first-of-type {
  margin-left: 50px;
}
.viewer-size-controls {
  float: right;
}
.viewer-frame {
  background-color: white;
  border: none;
  border-right: 1px solid #ccc;
}
.viewer-content {
  position: relative;
  background-color: #555;
}
.viewer-info {
  box-sizing: border-box;
  position: absolute;
  bottom: 0;
  background-color: #d3d3d3;
  width: 100%;
  overflow-y: scroll;
  padding: 1em;
  border-bottom: 1px solid #ccc;
  transition: height .5s;
}
.viewer-info-close {
  position: absolute;
  top: 1em;
  right: 1em;
}
.viewer-logo {
  max-width: 50px;
  max-height: 40px;
  padding: 5px .3em 0 .3em;
}
.viewer-tabs {
  list-style: none;
}
.viewer-tabs li {
  display: inline-block;
}
.viewer-tabs li + li {
  margin-left: 5px;
}
.viewer-button {
  background-color: #e8ecec;
  color: #1b363b;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 0.9em;
  width: 50px;
  height: 50px;
  vertical-align: top;
}
.viewer-button:before {
  font-size: 1.4em;
}
.viewer-button:hover {
  transition: all 0.2s ease-in;
  background-color: #ccd5d7;
  cursor: pointer;
}
.viewer-button:focus {
  outline: 0;
}
.viewer-button.is-active {
  background-color: #1b363b;
  color: #eddb60;
}
.viewer-button.is-active:hover {
  transition: all 0.2s ease-in;
  color: #ccd5d7;
}
.viewer-button-collapse-menu {
  display: none !important;
  background-color: #eddb60;
  color: #1b363b;
}
.viewer-button-collapse-menu:before {
  font-size: 2em;
}
.viewer-link {
  vertical-align: top;
  display: inline-block;
  width: 50px;
  height: 50px;
  font-size: 1.4em;
  background-color: #e8ecec;
  color: #1b363b;
  text-decoration: none;
  text-align: center;
}
.viewer-link:visited {
  color: #1b363b;
}
.viewer-link:hover {
  transition: all 0.2s ease-in;
  background-color: #ccd5d7;
  cursor: pointer;
}
.viewer-link:before {
  display: block;
  margin-top: 12.5px;
}
.viewer-size-controls .viewer-button {
  float: left;
  display: block;
  font-size: 1em;
  margin-left: 5px;
  font-weight: bold;
}
.viewer-size-input {
  height: 100%;
}
.viewer-icon,
.viewer-icon-info-outline,
.viewer-icon-launch,
.viewer-icon-label,
.viewer-icon-chevron-left,
.viewer-icon-expand-more,
.viewer-icon-view,
.viewer-icon-component,
.viewer-header-select-wrapper:before {
  font-family: 'google-material-icon-font-custom';
  font-weight: normal;
  font-style: normal;
}
.viewer-icon-info-outline:before {
  content: '\e639';
}
.viewer-icon-launch:before {
  content: '\e648';
}
.viewer-icon-label:before {
  content: '\e887';
}
.viewer-icon-chevron-left:before {
  content: '\e8a7';
}
.viewer-icon-expand-more:before {
  content: '\e8ab';
}
.viewer-icon-view:before {
  content: '\e699';
}
.viewer-icon-component:before {
  content: '\e628';
}
.viewer-width-input-wrapper {
  position: relative;
  display: block;
  float: left;
  margin-left: 5px;
}
.viewer-width-input-wrapper:before {
  position: absolute;
  right: 1.25em;
  content: 'px';
  height: 100%;
  font-size: 1em;
  font-weight: bold;
  line-height: 3.175em;
  z-index: 10;
}
.viewer-width-input {
  background: #f3f5f5;
  position: relative;
  height: 50px;
  padding: 0 2.5em 0 1em;
  width: 72px;
  font-size: 1em;
  font-weight: bold;
  letter-spacing: 0.5em;
  text-align: right;
  -webkit-appearance: none;
  border: none;
}
.viewer-header-select-wrapper {
  position: relative;
  display: inline-block;
  width: 250px;
  vertical-align: top;
  color: #1b363b;
  background: #e8ecec;
  padding: 0 0 0 50px;
  font-size: 1em;
  box-sizing: border-box;
  z-index: 1;
}
.viewer-header-select-wrapper:before {
  position: absolute;
  right: 0;
  width: 50px;
  height: 50px;
  padding-top: 13px;
  text-align: center;
  font-size: 1.4em;
  background-color: #e8ecec;
  content: '\e8ab';
  z-index: -1;
  border-left: 3px solid #f3f5f5;
}
.viewer-header-select-wrapper .viewer-model-type-icon {
  position: absolute;
  left: 0;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 2.2em;
  font-size: 1.4em;
}
.viewer-header-select {
  height: 50px;
  width: 100%;
  vertical-align: top;
  border: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: transparent;
  border-radius: 0;
  font-size: 1em;
  font-weight: bold;
}
.viewer-header-select:hover {
  cursor: pointer;
}
.viewer-header-select:focus {
  outline: none;
}
.viewer-header-select::-ms-expand {
  display: none;
  /* to ie 10 */
}
:-moz-any(.viewer-header-select-wrapper):before {
  background-color: #e8ecec;
  /* this is necessary for overcome the caret default browser */
  pointer-events: none;
  /* https://developer.mozilla.org/en-US/docs/Web/CSS/pointer-events  */
  z-index: 1;
  /* this is necessary for overcome the pseudo element */
}
